



































































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";

@Component({
  components: {
    Swipe
  },
  mixins: [mixin]
})
export default class User extends Vue {
  userInfo = {};
  user_active = "1";
  radio = "男";
  clickLogo(){
    let a:any = this.$refs['logo']
     a.click();
  }
  userSwitchover(user_active){
    this.user_active = user_active
  }
  oldPassword = ''
  newPassword = ''
  renewPassword = ''
recPassword(){
  console.log('调用',this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO].userId)
    if( (this.oldPassword || '') == ''){     
        this["$Message"].warning('请输入原密码');
        return 
    }
    if( (this.newPassword || '') == ''){     
        this["$Message"].warning('请输入新密码');
        return 
    }
    if( (this.renewPassword || '') == ''){     
        this["$Message"].warning('请确定密码');
        return 
    }
    if( (this.newPassword  !== this.renewPassword)){     
        this["$Message"].warning('密码不一致');
        return 
    }
  Vue.prototype.$reqFormPost1(
        "/user/password/update",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
          oldPassword:this.oldPassword,
          newPassword:this.newPassword,
      },
      res => {
        if (res.returnCode != 200) {
          this["$Message"].warning(res.message);
          return;
        }
        this["$Message"].success('保存成功');
        this.userInfo = res.data;
        this.queryuserinfo();
        console.log(res.data);
      }
    );
}
  changeLogo(){
   let a:any = this.$refs.logo
    let form = new FormData();
    form.append('file',a.files[0])
    Vue.prototype.$reqFormUpload(
      "/fileupload",
      form,
      res => {
      this.userInfo['userIcon'] = res.data.data.filename
      }
    );
  }
  
  queryuserinfo() {
    Vue.prototype.$reqFormPost1(
      "/user/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res.returnCode != 200) {
     this["$Message"].warning(res.message);
        return;
        }
        console.log("=-------------", res.data);
        this.userInfo = res.data;
        
        this.radio = this.userInfo['sex']
      }
    );
  }

  doUpdate(){
    Vue.prototype.$reqFormPost1(
        "/user/update",
        {
          userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .userId,
          token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .token,
            userIcon:this.userInfo['userIcon'],
            sex:this.radio,
            nickName:this.userInfo['nickName']
        },
        res => {
          if (res.returnCode != 200) {
            this["$Message"].warning(res.message);
            return;
          }
          this["$Message"].success('保存成功');
          this.userInfo = res.data;
          this.queryuserinfo();
          
        }
      );
  }
  mounted() {
    this.queryuserinfo();
  }
}



